body {
    font-family: arial, sans-serif;
}

.page-title {
    font-size: 2.5rem;
    background-color: #ff0048;
    text-align: center;
    padding: 1rem 0;
    color: white;
}

.container {
    width: 100%;
    height: 70vh;
    display: flex;
}

.editor-container {
    width: 60%;
    height: 100%;
    min-height: 100%;   
}

.editor-container iframe {
    height: 100%;
    min-height: 100%;  
}

.buttons-container {
    padding: 0 2rem;
    margin-top: 1rem;
}

.panels-container {
    display: flex;
    flex-direction: row;
    padding: 0 2rem;
    margin: 1rem 0;
    justify-content: space-between;
}

.layout-item {
    cursor: pointer;
    padding: 0.5rem 1rem;
    margin-left: 0.5rem;
    margin-top: 2px;
    border: 1px solid black;
    text-align: center;
}

.layout-item.selected {
    background-color: #ff0048;
    color: white;
    border-color: #ff0048;
}

.layout-item:hover {
    background-color: black;
    color: white;
}

.left-panel {
    width: 60%;
}

.right-panel {
    width: 40%;
}